// bg color is also needed in html in order to
// block body's background propagation
// see: https://stackoverflow.com/a/61265706
html,
body {
  background-color: rgb(19, 19, 19);
}

html {
  height: 100%;
}

body {
  color: rgb(255, 255, 255);
  font-family: system-ui, sans-serif;
  font-size: 1.1rem;
  line-height: 1.5;
  margin: 0;
  min-height: 100%;
}

h2,
h3,
h4,
h5 {
  font-family: monospace;
  margin-top: 2rem;
}

hr {
  margin: 2rem 0;
}

p {
  margin: 1rem 0;
}

li {
  margin: 0.4rem 0;
}

*:target {
  background-color: yellow;
}

a {
  color: yellow;
}

mark {
  background-color: white;
  color: black;
}

.w {
  max-width: 700px;
  margin: 0 auto;
  padding: 4rem 2rem;
}

hr {
  text-align: center;
  border: 0;

  &:before {
    content: '/////'
  }

  &:after {
    content: attr(data-content) '/////'
  }
}

table,
th,
td {
  width: 100%;
  border: thin solid rgb(63, 63, 63);
  border-collapse: collapse;
  padding: 0.4rem;
}

code,
pre {
  color: white;
  font-family: monospace;
  background-color: rgb(41, 41, 41);
  font-size: 0.9rem;
  margin: 1rem 0;
  white-space: pre-wrap;
}

div.highlighter-rouge code {
  display: block;
  overflow-x: auto;
  padding: 1rem;
}

blockquote {
  font-style: italic;
  border: thin solid black;
  padding: 1rem;

  p {
    margin: 0;
  }
}

img {
  max-width: 100%;
  display: block;
  margin: 0 auto;
}